.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.postsWrapper {
  border: 1px solid var(--main-color);
  border-radius: 3px;
  flex-grow: 1;
  overflow: auto;
  margin-bottom: 10px;
}

.postsManagement {
  display: flex;
  justify-content: space-between;
}
